import React,{useEffect,useState} from "react";
import styled from "styled-components";
import { useUser } from "../../context/userContext";
import remove from "../../../assets/remove.svg";
import camera from "../../../assets/camera.svg";
import gallery from "../../../assets/gallery.svg";
import Button from "../../general/button";
import Modal from "../../general/modal";
import useModal from "../../../hook/useModal";

export default function Profile() {
  const { state, dispatch } = useUser();
  const { isShowing, toggle } = useModal();
  const [selectedFile, setSelectedFile] = useState();
  const [preview, setPreview] = useState();

  useEffect(() => {
    if (!selectedFile) {
      setPreview(undefined);
      return;
    }

    const objectUrl = URL.createObjectURL(selectedFile);
    setPreview(objectUrl);
    // dispatch({ type: "SET_IMAGE", payload: preview });


    // free memory when ever this component is unmounted
    return () => URL.revokeObjectURL(objectUrl);
  }, [selectedFile]);

  const onSelectFile = (e) => {
    if (!e.target.files || e.target.files.length === 0) {
      setSelectedFile(undefined);
      return;
    }

    // I've kept this example simple by using the first image instead of multiple
    setSelectedFile(e.target.files[0]);
  };

  return (
    <Container>
      <Content>
        <Image show={state.userType} onClick={toggle}>
        {selectedFile && <img src={preview} alt="profile-picture" />}
        </Image>
        <Label color={state.userType}>
          <p className="title">
            {state.userType == "envoy"
              ? "نمایندۀ مجلس شورای اسلامی"
              : " ناظر نمایندگان"}{" "}
          </p>
          <p className="name">{`${state.firstName}   ${state.lastName}`}</p>
          <p className="edit" onClick={toggle}>ویرایش تصویر</p>
        </Label>
      </Content>
      <Modal isShowing={isShowing} hide={toggle} title="انتخاب عکس پروفایل ">
        <Input icon={gallery} text="انتخاب از گالری">
          <input
            type="file"
            onChange={onSelectFile}
          />
          <span></span>
        </Input>
        <Input icon={camera} text="عکاسی با دوربین">
          <input
            type="file"
            onChange={onSelectFile}
          />
          <span></span>
        </Input>
        <Input icon={remove}>
          <p className="text">حذف عکس</p>
          <span></span>
        </Input>

        <Box>
          <Button
            text="لغو"
            textColor="#095644"
            borderColor="#095644"
            width="35%"
            click={toggle}
          />
          <Button
            text="ثبت"
            textColor="#FFFFFF"
            background="#095644"
            width="62%"
            click={toggle}
          />
        </Box>
      </Modal>
    </Container>
  );
}

const Container = styled.div`
  background: #f3f3f3;
  margin-inline: -30%;
  margin-top: -19%;
  padding: 18px;
  @media (min-width: 480px) {
    margin: 0;
    background: inherit;
    margin-top: 5px;
    padding: 2.083vw 1.563vw;
  }
`;

const Content = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  @media (min-width: 480px) {
    justify-content: flex-start;
    gap: 20px;
  }
`;

const Image = styled.div`
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 4px solid #095644;
  box-shadow: 0px 6px 5px -3px rgba(0, 0, 0, 0.25);
  position: relative;
  &:after {
    content: "";
    display: ${(props) => (props.show == "envoy" ? "block" : "none")};
    position: absolute;
    width: 2.326vw;
    height: 2.326vw;
    border-radius: 2.326vw;
    background-color: #ff5a5a;
    top: -0.698vw;
    left: -1.837vw;
  }
  img {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    object-fit: contain;
  }
  @media (min-width: 480px) {
    width: 7.292vw;
    height: 7.552vw;
    padding: 4px;
    &:after{
      width:1.042vw;
      height:1.042vw;
      left: -0.837vw;
    }
    img {
      object-fit: cover;
    }
  }
`;

const Label = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
  .title {
    margin: 0;
    font-size: 2.791vw;
    font-weight: 300;
  }
  .name {
    margin: 0;
    font-weight: 700;
    font-size: 5.581vw;
  }
  .title,
  .name {
    color: ${(props) => (props.color == "envoy" ? "#095644" : "#707070")};
  }
  .edit {
    display: none;
  }
  @media (min-width: 480px) {
    .title {
      font-size: 1.042vw;
      color: #095644;
    }
    .name {
      font-size: 1.667vw;
    }
    .edit {
      display: block;
      color: #ffaa00;
      font-weight: 400;
      font-size: 1.25vw;
      margin: 0;
    }
  }
`;

const Input = styled.div`
  background: #eaeaea;
  border: 1px solid #eaeaea;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4.651vw;
  margin-bottom: 2.326vw;
  input{
    &::-webkit-file-upload-button {
      visibility: hidden;
    }
    &:before {
      content: "${(props) => props.text}";
      display: inline-block;
      color: #9f9f9f;
      text-align: center;
      outline: none;
      white-space: nowrap;
      -webkit-user-select: none;
      cursor: pointer;
      font-weight: 300;
      font-size: 4.651vw;
    }
  }
  input[type='file'] {
    color: rgba(0, 0, 0, 0);
    width: 100%;

  } 

  .text {
    margin: 0;
    color: #9f9f9f;
    font-weight: 300;
    font-size: 4.651vw;
  }
  span {
    width: 6.977vw;
    height: 6.977vw;
    background-image: url(${(props) => props.icon});
    background-size: contain;
    background-repeat: no-repeat;
  }
  @media(min-width:480px){
    padding:1.302vw;
    margin-bottom:1.302vw;
    input{
      &:before{
        font-size:1.458vw;
      }
    }
    .text{
      font-size:1.458vw;
    }
    span{
      width:1.823vw;
      height:1.823vw;
    }
  }
`;


const Box = styled.div`
  display: flex;
  gap: 10px;
  margin-top: 15px;
  @media(min-width:480px){
    justify-content:center;
    gap:1.563vw;
  }
`;